  function getAbilityUIRow(ability, defaultLevel) {
    defaultLevel = defaultLevel || ability.levelCap;

    var uiRow = $('<div/>', {'class': 'row ability', 'sylte': 'margin-bottom:10px;', 'data-buffed-id': ability.move.AffectedValueID, 'data-gem-type': ability.gemType, 'data-effects': JSON.stringify(ability.move.Effects)})
    .append(
      $('<input/>', {'type': 'hidden', 'value': ability.levels[defaultLevel-1].talentScore, 'name': 'talent-score'})
    )
    .append(
      $('<div/>', {'class': 'col-xs-3'})
      .append(
        $('<img/>', {'src': '/img/Gems/' + ability.gemType + '.png', 'width': 45, 'height': 45, 'alt': ability.gemType + ' Gem', 'class': 'center-block'})
      )
      .append(
        $('<p/>', {'html': '<span class="ability-mp">0</span> / <span class="ability-cost">' + ability.cost + '</span>', 'class': 'text-center'})
      )
      .append(
        $('<p/>', {'class': 'text-center'})
        .append(
          $('<button/>', {'class': 'btn btn-xs btn-primary btn-equip-ability', 'html': 'Equip'}).on('click', function() {
            var $abilityElement = $($(this).closest('.ability').first());
            if ($(this).hasClass('btn-primary')) {
              $(this).removeClass('btn-primary');
              $(this).addClass('btn-success');
              $abilityElement.addClass('equipped');
              $(this).text('Equipped');
            } else {
              $(this).removeClass('btn-success');
              $(this).addClass('btn-primary');
              $abilityElement.removeClass('equipped');
              $(this).text('Equip');
            }
            updateTalentUI();
            /*
            bootbox.alert("clicked!", function() {
              console.log("Alert Callback");
            });
            */
          })
        )
      )
    )
    .append(
      $('<div/>', {'class': 'col-xs-9'})
      .append(
        $('<div/>', {'class': 'row'})
        .append(
          $('<div/>', {'class': 'col-xs-12'})
          .append(
            $('<p/>')
            .append(
              $('<strong/>', {'text': ability.name})
            )
          )
        )
        .append(
          $('<div/>', {'class': 'col-xs-12'})
          .append(
            $('<p/>', {'html': ability.levels[defaultLevel-1].html, 'class': 'ability-text'})
          )
        )
        .append(
          $('<div/>', {'class': 'col-xs-11'})
          .append(
            $('<div/>', {'class': 'level-selector'})
            .append(
              $('<p/>')
              .append(
                $('<span/>', {'text': defaultLevel, 'class': 'abilitySelectedLevel'})
              )
              .append(
                '/'
              )
              .append(
                $('<span/>', {'text': ability.levelCap, 'class': 'abilityMaxLevel'})
              )
            )
            .append(
              $('<input/>', {'type': 'range', 'name': 'ability-level', 'min': 1, 'max': ability.levelCap, 'value': defaultLevel}).on('input', function() {
                var abilityLevel = $(this).val();
                var $curLevelSpan = $(this).parent().find('.abilitySelectedLevel').first();
                var $abilityElement = $($(this).closest('.ability').first());
                var $curAbilityDescription = $abilityElement.find('.ability-text').first();
                var $talentScore = $abilityElement.find('input[name="talent-score"]').first();

                var abilityID = $(this).next().val();
                var abilityItem = getAbilityForLevel(abilityID, abilityLevel);

                $curLevelSpan.text(abilityLevel);
                $curAbilityDescription.html(abilityItem.html);
                $talentScore.val(abilityItem.talentScore);
                $abilityElement.find('.ability-mp').first().text(0);
                updateStatsUI();
                applyActivatedBuffs($abilityElement);
              })
            )
            .append(
              $('<input/>', {'class': 'ability-id', 'name': 'ability-id', 'type': 'hidden', 'value': ability.ID})
            )
          )
        )
      )
    )

    return uiRow;
  }

  function updateAbilitiesUI(abilities, areCurrentValuesSaved) {
    var savedLevels = [];
    var savedEquipped = [0, 1, 2];
    if (areCurrentValuesSaved) {
      savedEquipped = [];
      $('input[name="ability-level"]').each(function(){
        savedLevels.push(parseInt($(this).val()));
      });
      $('.row.ability').each(function(i){
        if ($(this).hasClass('equipped')) {
          savedEquipped.push(i);
        }
      });
    }
    $('#abilities').empty();
    abilities.forEach(function(ability, index, array){
      if (areCurrentValuesSaved && savedLevels && savedLevels.length > 0) {
        var level = savedLevels[index];
        $('#abilities').append(getAbilityUIRow(ability, level));
      } else {
        $('#abilities').append(getAbilityUIRow(ability));
      }
    });
    equipAbilities(savedEquipped);
  }

  function getStatsForLevel(level) {
    for (var i in wrestlerDataItemSelected.Stats) {
      var thisWrestlerStats = wrestlerDataItemSelected.Stats[i];
      if (thisWrestlerStats.level == level) {
        return thisWrestlerStats.stats;
      }
    }
  }

  function getAbility(abilityID) {
    var theseAbilities = wrestlerDataItemSelected.Abilities;
    for (var i in theseAbilities) {
      if (theseAbilities[i].ID == abilityID) {
        return theseAbilities[i];
      }
    }
    return false;
  }

  function getAbilityForLevel(abilityID, abilityLevel) {
    var thisAbility = getAbility(abilityID);
    var thisAbilityLevels = thisAbility.levels;
    for (var i in thisAbilityLevels) {
      if (thisAbilityLevels[i].level == abilityLevel) {
        return thisAbilityLevels[i];
      }
    }
    return false;
  }

  function isBuffApplicableToAbility(effect, effects, abilityTextElement, buffedID, buffGemType, moveGemType, buffMoveType){
    /*
    console.log("effect: " + effect);
    console.log("effects: " + effects);
    console.log("abilityTextElement: " + abilityTextElement);
    console.log("buffedID: " + buffedID);
    console.log("buffGemType: " + buffGemType);
    console.log("moveGemType: " + moveGemType);
    console.log("buffMoveType: " + buffMoveType);
    console.log("----------");
    */
    if (buffMoveType == "Move_StartCharge_Abs") {
      if (buffGemType == 'Rainbow' || buffGemType == moveGemType) {
        return true;
      }
    } else if ($.inArray(effect, effects) !== -1) {
      if (effect == 'Generate' || effect == 'GemDestroy') {
        //check for generate
        var buffedElement = abilityTextElement.find("span[data-variable-name='"+buffedID+"']");
        if (buffedElement && buffedElement.length) {
          var buffedValue = buffedElement.first().attr("data-variable-value");
          if (buffGemType == buffedValue) {
            return true;
          }
        } else if (buffGemType == moveGemType) {
          return true;
        }
      } else if (effect == 'MoveEffectImprove') {
        //check for damage
        if (buffGemType == 'Rainbow' || buffGemType == moveGemType) {
          return true;
        }
      } else if (effect == 'MoveStealGemImprove') {
        var buffedElement = abilityTextElement.find("span[data-variable-name='"+buffedID+"']");
        if (buffedElement && buffedElement.length) {
          var buffedValue = buffedElement.first().attr("data-variable-value");
          if (buffGemType == buffedValue) {
            return true;
          }
        }
      } else if (effect == 'GemEffectImprove') {
        var buffedElement = abilityTextElement.find("span[data-variable-name='"+buffedID+"']");
        if (buffedElement && buffedElement.length) {
          var buffedValue = buffedElement.first().attr("data-variable-value");
          if (buffMoveType == 'ModGem_Effect_Strike_Perc' && buffedValue == 'Strike') {
            return true;
          }
          if (buffMoveType == 'ModGem_Effect_Heal_Perc' && buffedValue == 'Heal') {
            return true;
          }
          if (buffMoveType == 'ModGem_Effect_Block_Perc' && buffedValue == 'Block') {
            return true;
          }
        }
      } else if (effect == 'MoveSubmissionDamage') {
        if (buffGemType == 'Rainbow' || buffGemType == moveGemType) {
          return true;
        }
      } else if (effect == 'GemDamage') {
        var buffedElement = abilityTextElement.find('span[data-variable-name="min2"]');
        if (buffedElement && buffedElement.length) {
          var buffedValue = buffedElement.first().attr("data-variable-value");
          if (buffGemType == 'Rainbow' || buffedValue == buffGemType) {
            return true;
          }
        }
      } else if (effect == 'MoveSubmissionTurn') {
        if (buffGemType == 'Rainbow' || buffGemType == moveGemType) {
          return true;
        }
      } else if (effect == 'MoveStealImprove' || effect == 'MoveBleedImprove') {
        return true;
      }
      return false;
    }
    return false;
  }

